.taskInput {
  width: 100%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.taskInput form {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.taskInput input {
  height: 26px;
  font-size: 16px;
  font-style: italic;
}

.taskInput input:focus {
  height: 26px;
  font-size: 16px;
  font-style: normal;
  border: 2px solid rgb(0, 188, 212);
}

@media (max-width: 520px) {
  
  .taskInput {
    width: 100%;
    height: 70px;
  }

  .taskInput form {
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}


