.searchContainer {
  height: 50px;
  width: 100%;
  background: rgba(0, 188, 212, 0.1);
    overflow: hidden;
}

.logos {

}

.searchBar {
  height: 50px;
  width: 60%;
  margin-right: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.searchCheckBox {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.searchBar span {
  text-align: right;
  width: 50px;
  font-size: 14px;
}

.searchInput {
  display: block;
  height: 20px;
  margin: 0;
  font-size: 14px;
  font-style: italic;
  outline: none;
}

.searchInput:focus {
  height: 20px;
  font-size: 14px;
  font-style: normal;
  border: 2px solid rgb(0, 188, 212);
}


