.categoryInput {
  width: 100%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.categoryInput form {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

.categoryInput input {
  min-width: 50px;
  height: 26px;
  font-size: 16px;
  font-style: italic;
}

.categoryInput input:focus {
  height: 26px;
  font-size: 16px;
  font-style: normal;
  border: 2px solid rgb(0, 188, 212);
}

@media (max-width: 520px) {
  
  .categoryInput {
    width: 100%;
    height: 70px;
  }

  .categoryInput form {
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



